'use client'
import { useEffect, useState, useRef } from 'react'
import 'chartjs-chart-matrix'
import { Chart, registerables } from 'chart.js'
Chart.register(...registerables)
import Head from 'next/head'
import D3ConfusionMatrix from '../../components/D3ConfusionMatrix'
import { getKPIs, getErrorData } from '../../services/dummyDbService'

interface KPI {
  rowKey: string
  value: string
  status?: string
}

interface PlotDataPoint {
  x: string
  y: number
}

interface ErrorData {
  timePeriod: string
  meanPrediction: number
  error: number
  exceedsThreshold: boolean
  predicted?: string
  actual?: string
  yTrue?: number
  yPred?: number
  percentageError?: number
  plotData?: PlotDataPoint[]
}

interface ConfusionMatrixData {
  reference: {
    trueA: number
    falseB: number
    falseA: number
    trueB: number
    precision: number
    recall: number
    f1: number
    accuracy: number
  }
  current: {
    trueA: number
    falseB: number
    falseA: number
    trueB: number
    precision: number
    recall: number
    f1: number
    accuracy: number
  }
}


export default function Mode4Page() {
  const chartRef = useRef<Chart | null>(null)
  const [businessUnit, setBusinessUnit] = useState('CCS')
  const [useCase, setUseCase] = useState('CC-Di')
  const [kpis, setKpis] = useState<KPI[]>([])
  const [errors, setErrors] = useState<ErrorData[]>([])
  const [confusionMatrix, setConfusionMatrix] = useState<ConfusionMatrixData>({
    reference: {
      trueA: 0,
      falseB: 0,
      falseA: 0,
      trueB: 0,
      precision: 0,
      recall: 0,
      f1: 0,
      accuracy: 0
    },
    current: {
      trueA: 0,
      falseB: 0,
      falseA: 0,
      trueB: 0,
      precision: 0,
      recall: 0,
      f1: 0,
      accuracy: 0
    }
  })
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const initData = async () => {
      try {
        const savedBusinessUnit = localStorage.getItem('businessUnit')
        const savedUseCase = localStorage.getItem('useCase')
        if (savedBusinessUnit) setBusinessUnit(savedBusinessUnit)
        if (savedUseCase) setUseCase(savedUseCase)

        const [kpiData, errorData] = await Promise.all([
          getKPIs('mode4'),
          getErrorData() // Removed mode4 argument since function expects none
        ])
        setKpis(kpiData)
        setErrors(errorData.map(e => ({
          timePeriod: e.timePeriod || '',
          meanPrediction: Number(e.meanPrediction) || 0,
          error: Number(e.error) || 0,
          exceedsThreshold: Boolean(e.exceedsThreshold)
        })))
        
        // Extract current confusion matrix data from KPIs
        const matrixData: ConfusionMatrixData = {
          reference: {
            trueA: Number(kpiData.find(k => k.rowKey === 'refTrueA')?.value) || 0,
            falseB: Number(kpiData.find(k => k.rowKey === 'refFalseB')?.value) || 0,
            falseA: Number(kpiData.find(k => k.rowKey === 'refFalseA')?.value) || 0,
            trueB: Number(kpiData.find(k => k.rowKey === 'refTrueB')?.value) || 0,
            precision: Number(kpiData.find(k => k.rowKey === 'refPrecision')?.value) || 0,
            recall: Number(kpiData.find(k => k.rowKey === 'refRecall')?.value) || 0,
            f1: Number(kpiData.find(k => k.rowKey === 'refF1')?.value) || 0,
            accuracy: Number(kpiData.find(k => k.rowKey === 'refAccuracy')?.value) || 0
          },
          current: {
            trueA: Number(kpiData.find(k => k.rowKey === 'currTrueA')?.value) || 0,
            falseB: Number(kpiData.find(k => k.rowKey === 'currFalseB')?.value) || 0,
            falseA: Number(kpiData.find(k => k.rowKey === 'currFalseA')?.value) || 0,
            trueB: Number(kpiData.find(k => k.rowKey === 'currTrueB')?.value) || 0,
            precision: Number(kpiData.find(k => k.rowKey === 'currPrecision')?.value) || 0,
            recall: Number(kpiData.find(k => k.rowKey === 'currRecall')?.value) || 0,
            f1: Number(kpiData.find(k => k.rowKey === 'currF1')?.value) || 0,
            accuracy: Number(kpiData.find(k => k.rowKey === 'currAccuracy')?.value) || 0
          }
        }
        
        setConfusionMatrix(matrixData)
      } catch (error) {
        console.error('Error loading data:', error)
      } finally {
        setLoading(false)
      }
    }
    initData()
  }, [])

  const handleBusinessUnitChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const value = e.target.value
    setBusinessUnit(value)
    setUseCase('')
    localStorage.setItem('businessUnit', value)
  }

  const handleUseCaseChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const value = e.target.value
    setUseCase(value)
    localStorage.setItem('useCase', value)
  }

  return (
    <div className="bg-gray-900 min-h-screen flex flex-col">
      <Head>
        <title>Mode 4 | CLCD Dashboard</title>
        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0-beta3/css/all.min.css" />
      </Head>

      <main className="flex-grow container mx-auto px-4 py-8">
        <div className="space-y-6">
          <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 border border-gray-700">
            <h2 className="text-2xl font-semibold text-blue-300 mb-4">OCTAVE - CLCD</h2>
          
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">Business Unit</h3>
              <select
                className="w-full bg-gray-700 border-blue-600 rounded p-2 text-white"
                value={businessUnit}
                onChange={handleBusinessUnitChange}
              >
                <option value="CCS">CCS</option>
                <option value="JMSL">JMSL</option>
              </select>
            </div>

            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">Use Case</h3>
              <select
                className="w-full bg-gray-700 border-blue-600 rounded p-2 text-white"
                value={useCase}
                onChange={handleUseCaseChange}
              >
                <option value="Distribution Efficiency">Distribution Efficiency</option>
                <option value="Load Forecasting">Load Forecasting</option>
              </select>
            </div>

            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">Short Code</h3>
              <input
                type="text"
                value={businessUnit && useCase ? `${businessUnit.substring(0,2)}-${useCase.substring(0,2)}` : '-'}
                readOnly
                className="w-full bg-gray-700 border-blue-600 rounded p-2 text-white"
              />
            </div>

            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">Runtime</h3>
              <input
                type="text"
                value={loading ? 'Loading...' : kpis.find(k => k.rowKey === 'runtime')?.value || 'N/A'}
                readOnly
                className="w-full bg-gray-700 border-blue-600 rounded p-2 text-white"
              />
            </div>
          </div>

          </div>
        </div>

        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">Hyperparameters</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">Hyperparameters</h3>
              <p className="text-xl">N/A</p>
            </div>
            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">Status</h3>
              <p className="text-xl">N/A</p>
            </div>
          </div>
        </div>

        {/* Drift Warning Plot Section */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">Drift Warning Plot</h2>
            <div className="h-64 bg-gray-700 rounded p-4">
              {loading ? (
                <div className="flex items-center justify-center h-full">
                  <div className="text-white">Loading drift warnings...</div>
                </div>
              ) : (
                <div className="relative h-full">
                  <canvas 
                    id="driftWarningChart"
                    className="w-full h-full"
                    ref={(el) => {
                      if (el && !loading) {
                        const ctx = el.getContext('2d');
                        if (ctx) {
                          if (chartRef.current) {
                            chartRef.current.destroy();
                          }
                          chartRef.current = new Chart(ctx, {
                            type: 'line',
                            data: {
                              labels: errors.length > 0 ? errors.map((e, i) => e.timePeriod || `Time ${i + 1}`) : [],
                              datasets: [
                                {
                                  label: 'Drift Level', 
                                  data: errors.length > 0 
                                    ? errors.map(point => Math.floor(point.yPred || 0) % 3) // Ensures only 0,1,2 with fallback
                                    : [],
                                  borderColor: 'rgb(239, 68, 68)',
                                  backgroundColor: 'rgba(239, 68, 68, 0.1)',
                                  tension: 0.1,
                                  fill: true
                                }
                              ]
                            },
                            options: {
                              responsive: true,
                              maintainAspectRatio: false,
                              plugins: {
                                legend: {
                                  labels: {
                                    color: '#e5e7eb'
                                  }
                                },
                                tooltip: {
                                  mode: 'index',
                                  intersect: false
                                }
                              },
                              scales: {
                                x: {
                                  title: {
                                    display: true,
                                    text: 'Data Points',
                                    color: '#93c5fd',
                                    font: {
                                      weight: 'bold'
                                    }
                                  },
                                  grid: {
                                    color: 'rgba(255, 255, 255, 0.1)'
                                  },
                                  ticks: {
                                    color: '#e5e7eb',
                                    font: {
                                      size: 12
                                    }
                                  }
                                },
                                y: {
                                  title: {
                                    display: true,
                                    text: 'Drift Value',
                                    color: '#93c5fd',
                                    font: {
                                      weight: 'bold'
                                    }
                                  },
                                  beginAtZero: true,
                                  grid: {
                                    color: 'rgba(255, 255, 255, 0.1)'
                                  },
                                  ticks: {
                                    color: '#e5e7eb',
                                    font: {
                                      size: 12
                                    },
                                    callback: (value) => typeof value === 'number' ? value.toFixed(2) : value
                                  }
                                }
                              },
                              animation: {
                                duration: 1000,
                                easing: 'easeInOutQuad'
                              },
                              elements: {
                                point: {
                                  radius: 4,
                                  hoverRadius: 6
                                }
                              }
                            }
                          });
                        }
                      }
                    }}
                  />
                </div>
              )}
            </div>
        </div>

        {/* XAI Result Section */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">XAI Result</h2>
          <div className="space-y-3 text-white">
            <p>The current model shows decreased performance compared to reference values, particularly in precision (8.2% decrease) and recall (8.7% decrease). The confusion matrices reveal increased misclassification rates between Class A and Class B.</p>
            <ul className="list-disc pl-5 space-y-1">
              <li>Class A being predicted as Class B increased by 140% (from 5% to 12%)</li>
              <li>Class B being predicted as Class A increased by 166% (from 3% to 8%)</li>
            </ul>
            <p>The model's F1 score has correspondingly decreased by 8% (from 0.88 to 0.81).</p>
            <p className="font-medium text-yellow-300">These metrics suggest the model may require retraining or parameter adjustment.</p>
          </div>
        </div>

        {/* Error and Misclassification Percentages */}
        <div className="bg-red-900/20 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-red-800/50">
          <h2 className="text-2xl font-semibold text-red-300 mb-4">Error and Misclassification Percentages</h2>
          <div className="space-y-3 text-white">
            <p>Overall error rate has increased from 8% to 16% compared to reference data.</p>
            <div className="w-full bg-gray-700 rounded-full h-4">
              <div className="bg-red-500 h-4 rounded-full" style={{width: '16%'}}></div>
            </div>
            <p>Key changes in misclassification:</p>
            <ul className="list-disc pl-5 space-y-1">
              <li>Class A → Class B: +140% (from 5% to 12%)</li>
              <li>Class B → Class A: +166% (from 3% to 8%)</li>
            </ul>
          </div>
        </div>

        {/* Misclassified Table */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">Misclassified Table</h2>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-700">
              <thead>
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">ID</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">Predicted</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">Actual</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-700">
                {loading ? (
                  <tr>
                    <td colSpan={3} className="px-6 py-4 text-center text-sm text-white">
                      Loading misclassified data...
                    </td>
                  </tr>
                ) : errors.length > 0 ? (
                  errors.map((error, index) => (
                    <tr key={index} className="bg-red-900/10">
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-white">{index + 1}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-red-300">{error.predicted}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-white">{error.actual}</td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={3} className="px-6 py-4 text-center text-sm text-white">
                      No misclassified data available
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </main>
    </div>
  )
}
